.upload-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.8);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
  backdrop-filter: blur(5px);
  overflow-y: auto;
  padding: 20px;
}

.upload-modal {
  background: linear-gradient(135deg, #1a1a1a, #0a0a0a);
  border-radius: 20px;
  padding: 40px;
  width: 90%;
  max-width: 500px;
  border: 1px solid rgba(255, 216, 0, 0.3);
  position: relative;
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.5);
  max-height: 90vh;
  overflow-y: auto;
}

.close-btn {
  position: absolute;
  top: 15px;
  right: 15px;
  background: none;
  border: none;
  color: #FFD800;
  font-size: 1.5rem;
  cursor: pointer;
  padding: 5px;
  border-radius: 50%;
  transition: all 0.3s ease;
}

.close-btn:hover {
  background: rgba(255, 216, 0, 0.1);
  transform: rotate(90deg);
}

.upload-modal h2 {
  color: #FFD800;
  text-align: center;
  margin-bottom: 30px;
  font-size: 2rem;
  font-weight: 700;
}

.upload-tabs {
  display: flex;
  margin-bottom: 30px;
  border-radius: 10px;
  overflow: hidden;
  border: 1px solid rgba(255, 216, 0, 0.3);
}

.upload-tabs button {
  flex: 1;
  padding: 15px;
  background: rgba(255, 255, 255, 0.05);
  border: none;
  color: white;
  cursor: pointer;
  transition: all 0.3s ease;
  font-size: 1rem;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
}

.upload-tabs button.active {
  background: linear-gradient(45deg, #FFD800, #FFA500);
  color: black;
  font-weight: 600;
}

.upload-tabs button:not(.active):hover {
  background: rgba(255, 216, 0, 0.1);
}

.error-message {
  background: rgba(255, 0, 0, 0.1);
  border: 1px solid rgba(255, 0, 0, 0.3);
  color: #ff6b6b;
  padding: 10px;
  border-radius: 10px;
  margin-bottom: 20px;
  text-align: center;
}

.success-message {
  background: rgba(0, 255, 0, 0.1);
  border: 1px solid rgba(0, 255, 0, 0.3);
  color: #4caf50;
  padding: 10px;
  border-radius: 10px;
  margin-bottom: 20px;
  text-align: center;
}

.form-group {
  margin-bottom: 20px;
}

.form-group input,
.form-group textarea {
  width: 100%;
  padding: 15px;
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(255, 216, 0, 0.3);
  border-radius: 10px;
  color: white;
  font-size: 1rem;
  transition: all 0.3s ease;
  box-sizing: border-box;
  font-family: inherit;
}

.form-group input:focus,
.form-group textarea:focus {
  outline: none;
  border-color: #FFD800;
  box-shadow: 0 0 0 2px rgba(255, 216, 0, 0.2);
}

.form-group input::placeholder,
.form-group textarea::placeholder {
  color: rgba(255, 255, 255, 0.5);
}

.file-upload-area {
  margin-bottom: 15px;
}

.file-upload-label {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
  padding: 40px;
  border: 2px dashed rgba(255, 216, 0, 0.3);
  border-radius: 10px;
  cursor: pointer;
  transition: all 0.3s ease;
  color: #e0e0e0;
  font-size: 1.1rem;
}

.file-upload-label:hover {
  border-color: #FFD800;
  background: rgba(255, 216, 0, 0.05);
}

.file-upload-label i {
  font-size: 1.5rem;
  color: #FFD800;
}

.image-preview {
  margin-top: 15px;
  text-align: center;
}

.image-preview img {
  max-width: 100%;
  max-height: 200px;
  border-radius: 10px;
  border: 1px solid rgba(255, 216, 0, 0.3);
}

.submit-btn {
  width: 100%;
  padding: 15px;
  background: linear-gradient(45deg, #FFD800, #FFA500);
  border: none;
  border-radius: 10px;
  color: black;
  font-size: 1.1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
}

.submit-btn:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: 0 5px 15px rgba(255, 216, 0, 0.4);
}

.submit-btn:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}

@media (max-width: 480px) {
  .upload-modal {
    padding: 30px 20px;
    margin: 10px;
  }
  
  .upload-tabs {
    flex-direction: column;
  }
  
  .file-upload-label {
    padding: 30px 20px;
  }
}